import { createStore } from 'redux-dynamic-modules';
import { getSagaExtension } from "redux-dynamic-modules-saga";

// const store = createStore({
//     initialState: { /** initial state */ },
//     enhancers: [ /** enhancers to include */],
//     extensions: [/** extensions to include i.e. getSagaExtension(), getThunkExtension() */],
// },
//     /* ...any additional modules */
// );

const store = createStore({
    extensions: [getSagaExtension()],
}, [], [], []);

const reduxDispatch = (type, payload) => {
    return new Promise((resolve, reject) => {
        if (type !== undefined) {
            store.dispatch({ type, payload, resolve, reject });
        }
    });
}

export { store, reduxDispatch };