import * as actionTypes from './action';

const initState = {
    user: '',
    value: false,
    ispassdoorman: false,
    isLogging: false,
}

function reducer(state = initState, action) {
    switch (action.type) {
        case actionTypes.SET_LOGIN_USER:
            return {
                ...state,
                user: action.payload
            };
        case actionTypes.LOGIN:
            return {
                ...state,
                isLogging: true,
            };

        case actionTypes.LOGOUT:
            return {
                ...state,
                isLogging: false,
            };

        case actionTypes.PASS_INDEX_PAGE:
            return {
                ...state,
                ispassdoorman: action.payload
            };
        default:
            break;
    }

    return state;
}

export default reducer;